<template>
  <ion-page>
    <ion-content>
      <ion-card
        style="padding-top: 50px"
        class="px-10 py-5 ion-text-center ion-margin-bottom"
      >
        <ion-img
          src="assets/Malawi-Coat_of_arms_of_arms.png"
          alt="app-logo"
          style="margin: 0px auto; width: 10%"
        ></ion-img>

        <ion-title style="font-size: 35px; font-weight: bold; margin-top: 20px">
          IBLIS
        </ion-title>

        <ion-list class="ion-margin-top">
          <ion-title style="font-size: 25px">
            Select Laboratory Location
          </ion-title>

          <ion-row class="ion-grid ion-gap-5">
            <ion-col
              size="6"
              v-for="location in locations"
              v-bind:key="location.name"
              @click="getLocation(location)"
              class="ion-text-center ion-padding-vertical ion-bg-gray-50 ion-text-zinc-500 ion-border ion-rounded ion-hover-bg-gray-100 ion-hover ion-cursor-pointer"
              style="cursor: pointer"
            >
              <ion-card
                color="light"
                style="
                  padding-top: 60px;
                  padding-bottom: 60px;
                  position: relative;
                "
              >
                <ion-img
                  src="/assets/lab.png"
                  alt="lab-icon"
                  style="margin: 0px auto; width: 10%"
                ></ion-img>
                <ion-label
                  style="
                    font-size: 22px;
                    font-weight: 500;
                    margin-top: 20px !important;
                  "
                >
                  {{ location.name }}
                </ion-label>
                <div
                  style="position: absolute; top: 2px; left: 2px"
                  v-if="selectedLocation == location"
                >
                  <ion-icon
                    :icon="checkbox"
                    size="large"
                    color="success"
                  ></ion-icon>
                </div>
              </ion-card>
            </ion-col>
          </ion-row>
        </ion-list>

        <ion-button
          expand="full"
          color="primary"
          size="large"
          :disabled="selectedLocation == undefined"
          @click="navigateHome"
        >
          Continue &rarr;
        </ion-button>
      </ion-card>
    </ion-content>
  </ion-page>
</template>

<script setup lang="ts">
import { Client } from "@/composables/client";
import { toastDanger, toastSuccess } from "@/utils/Alerts";
import {
  IonImg,
  IonCard,
  IonLabel,
  IonButton,
  IonPage,
  IonContent,
  IonList,
  IonIcon,
  IonRow,
  IonCol,
  IonTitle,
} from "@ionic/vue";
import { checkbox } from "ionicons/icons";
import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";

type Location = {
  id: number;
  name: string;
};

const { token, location } = Client();
const client = Client;
const router = useRouter();

const data = token.value?.authorization?.user;
const locations = ref<Location[]>(data.lab_locations);
const selectedLocation = ref<Location>();
const getLocation = (location: Location) => {
  selectedLocation.value = location;
  console.log(location);
};
const navigateHome = (): void => {
  if (client().updateLocation(String(selectedLocation?.value?.name))) {
    toastSuccess(`Logged in from ${selectedLocation?.value?.name} Reception`);
    router.push("/home");
  } else toastDanger("An error occurred, please try again");
};

onMounted(() => {
  if (data.lab_locations.length == 1) {
    selectedLocation.value = locations.value[0];
    client().updateLocation(String(locations.value[0].name));
    toastSuccess(`Logged in from ${location.value} Reception`);
    router.push("/home");
  }
});
</script>

<style scoped></style>
